@import '../../styles/mixins';

.sb-input {
  background-color: var(--sbt-color-background);
  border: var(--sbt-input-border-size) solid var(--sbt-input-border-color);
  border-radius: var(--sbt-input-border-radius);
  color: var(--sbt-theme-tertiary-color-90);
  font-size: var(--sb-input-text-size, var(--sbt-font-size-30));
  font-weight: var(--sb-input-text-weight, var(--sbt-font-weight-40));
  height: var(--sbt-input-height);
  line-height: var(--sbt-line-height-100);
  padding: 0 0.5rem;
  transition: all 80ms cubic-bezier(0.2, 0, 0.4, 0.9);

  &::placeholder {
    color: var(--sb-select__placeholder);
  }

  &:focus {
    border-color: transparent;
    box-shadow: var(--sbt-input-box-shadow);
    outline: none;
    outline-color: var(--sbt-theme-color-40);
  }

  &.sb-input__is-disabled,
  &:disabled,
  &:read-only {
    @include disabled-shake();
    background-color: var(--sbt-color-disabled-background);
    border: var(--sbt-input-border-size) solid var(--sbt-color-disabled-border);
    box-shadow: none;
    color: var(--sbt-color-disabled-text);
    cursor: not-allowed;

    &::placeholder {
      color: var(--sbt-color-disabled-text);
    }

    &:hover {
      background-color: var(--sbt-theme-tertiary-color-10);
      border: var(--sbt-input-border-size) solid
        var(--sbt-theme-tertiary-color-30);
      color: var(--sbt-color-disabled-text);
    }
  }

  // fix iOS devices, which add custom styling that causes borders to disappear on focus
  @supports (-webkit-touch-callout: none) {
    -webkit-appearance: none;
  }
}

.sb-input_error {
  @include error();
}

.sb-input__size-small {
  width: 180px;
}

.sb-input__size-medium {
  width: 300px;
}

.sb-input__size-large {
  width: 500px;
}

.sb-input__size-auto {
  width: 100%;
}

.sb-input__flush {
  border-radius: 0;
}

.sb-input__flush-right {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.sb-input__flush-left {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.sb-input__borderless {
  --sbt-input-border-size: 0;
}
